import React from 'react'
import dental from '../../../assets/dental.mp4'
import { Center, Heading,Text } from '@chakra-ui/react'
const HomeVideo = () => {
  return (
    <div>
        
        <video autoPlay loop muted id='video' preload="auto" >
        <source src={dental} type='video/mp4'/>

        </video>

        <Center
        position="absolute"
        top={0}
        left={0}
        width="100%"
         height="100vh"
        // bg="rgba(0, 0, 0, 0.5)"
        color="black"
        flexDirection="column"
        alignItems="center"
        justifyContent="center"
        textAlign="center"
      >
        {/* Add your overlay content here */}
        <Text fontSize="2xl">Overlay Title</Text>
        <Text>This is some overlay content.</Text>
      </Center>
    </div>
  )
}

export default HomeVideo